<template>
  <div>
    <div class="nav">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/userinfo/alluser' }"
          >所有用户信息</el-breadcrumb-item
        >
        <el-breadcrumb-item>{{ details.username }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-form
      :model="details"
      ref="details"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-row>
        <el-col :span="4">
          <el-form-item label="用户姓名" prop="username">
            <el-input
              v-model="details.username"
              :label-width="formLabelWidth"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="联系电话" prop="call">
            <el-input v-model="details.call" label-width="140px"></el-input
          ></el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="身份证号" prop="sfz">
            <el-input
              v-model="details.sfz"
              label-width="140px"
              maxlength="18"
              @blur="sex"
            ></el-input
          ></el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="性别" prop="sex">
            <el-input
              v-model="details.sex"
              label-width="140px"
              :disabled="true"
            ></el-input
          ></el-form-item>
        </el-col>

        <el-col :span="4">
          <el-form-item label="生日" prop="birthday">
            <el-input
              v-model="details.birthday"
              label-width="140px"
              :disabled="true"
            ></el-input
          ></el-form-item>
        </el-col>
      </el-row>

      <el-form-item label="详细地址" prop="address">
        <el-input
          v-model="details.address"
          :label-width="formLabelWidth"
        ></el-input
      ></el-form-item>
      <el-form-item label="是否购车">
        <el-switch
          v-model="details.buy"
          @change="changeStatus"
          style="marign-right: 120px"
        ></el-switch>
      </el-form-item>

      <!-- 默认false if yes to true -->
      <div :style="{ display: show }">
        <el-form-item label="购车时间">
          <el-col :span="6">
            <el-date-picker
              type="date"
              placeholder="购车时间"
              v-model="details.date1"
              style="width: 100%"
            ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-col :span="6">
          <el-form-item label="车辆品牌" prop="Moto">
            <el-input
              v-model="details.Moto"
              :label-width="formLabelWidth"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="车辆型号" prop="MotoType">
            <el-input v-model="details.MotoType" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="6">
          <el-form-item label="车牌号" prop="brand">
            <el-input v-model="details.brand" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :style="{ display: show_jg ? '' : 'none' }">
          <el-form-item label="购车价格" prop="Money">
            <el-input v-model="details.Money" autocomplete="off"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :style="{ display: show_jg ? 'none' : '' }">
          <el-button round @click="jg">info</el-button></el-col
        >
      </div>
      <!-- 买保险 -->
      <el-col :span="24">
        <el-col :span="8">
          <el-form-item label="购保时间">
            <div class="block">
              <el-date-picker
                type="date"
                v-model="details.date"
                style="width: 90%"
                placeholder="选择日期"
              >
              </el-date-picker>
            </div>
          </el-form-item>
        </el-col>
        <!-- 审车 -->
        <el-col :span="8">
          <el-form-item label="首次审车时间">
            <div class="block">
              <el-date-picker
                v-model="details.date_sc"
                type="date"
                style="width: 90%"
                placeholder="选择日期"
              >
              </el-date-picker>
            </div>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="4">
            <el-form-item label="本年购保情况" prop="bx_true">
              <el-switch
                style="display: block"
                v-model="details.bx_true"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text="已购"
                inactive-text="未购"
              >
              </el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item label="本年审车情况" prop="sc_true">
              <el-switch
                style="display: block"
                v-model="details.sc_true"
                active-color="#13ce66"
                inactive-color="#ff4949"
                active-text="已审"
                inactive-text="未审"
              >
              </el-switch>
            </el-form-item>
          </el-col>
        </el-col>
      </el-col>
      <el-col :span="6">
        <el-form-item label="行驶证图片：" prop="img">
          <div class="block">
            <el-col :span="24">
              <div id="ddd">
                <el-image :src="details.img" style="width: 80%">
                  <div slot="error" class="image-slot">暂无图片！</div>
                </el-image>
              </div>
            </el-col>
            <el-button type="info" round size="small" @click="ed(0)"
              >新增/修改</el-button
            >
            <el-button
              type="primary"
              round
              size="small"
              @click="bigimg(0)"
              :style="{ display: this.details.img ? '' : 'none' }"
              >查看大图</el-button
            >
          </div>
        </el-form-item>
        <el-form-item label="" prop="img_xszfm">
          <div class="block">
            <el-col :span="24">
              <el-image :src="details.img_xszfm" style="width: 80%">
                <div slot="error" class="image-slot">暂无图片！</div>
              </el-image>
            </el-col>
            <el-button type="info" round size="small" @click="ed(1)"
              >新增/修改</el-button
            >
            <el-button
              type="primary"
              round
              size="small"
              @click="bigimg(1)"
              :style="{ display: this.details.img_xszfm ? '' : 'none' }"
              >查看大图</el-button
            >
          </div>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="车辆合格证：" prop="img_qs">
          <div class="block">
            <el-col :span="24">
              <el-image :src="details.img_qs" style="width: 80%">
                <div slot="error" class="image-slot">暂无图片！</div>
              </el-image>
            </el-col>
            <el-button type="info" round size="small" @click="ed(2)"
              >新增/修改</el-button
            >
            <el-button
              type="primary"
              round
              size="small"
              @click="bigimg(2)"
              :style="{ display: this.details.img_qs ? '' : 'none' }"
              >查看大图</el-button
            >
          </div>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="新车合照：" prop="img_new">
          <div class="block">
            <el-col :span="24">
              <el-image :src="details.img_new" style="width: 80%">
                <div slot="error" class="image-slot">暂无图片！</div>
              </el-image>
            </el-col>
            <el-button type="info" round size="small" @click="ed(3)"
              >新增/修改</el-button
            >
            <el-button
              type="primary"
              round
              size="small"
              @click="bigimg(3)"
              :style="{ display: this.details.img_new ? '' : 'none' }"
              >查看大图</el-button
            >
          </div>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="驾驶证：" prop="img_jsz">
          <div class="block">
            <el-col :span="24">
              <el-image :src="details.img_jsz" style="width: 80%">
                <div slot="error" class="image-slot">暂无图片！</div>
              </el-image>
            </el-col>
            <el-button type="info" round size="small" @click="ed(4)"
              >新增/修改</el-button
            >
            <el-button
              type="primary"
              round
              size="small"
              @click="bigimg(4)"
              :style="{ display: this.details.img_jsz ? '' : 'none' }"
              >查看大图</el-button
            >
          </div>
        </el-form-item>
      </el-col>
      <!-- 修改照片 -->

      <el-dialog title="修改图片" :visible.sync="dialogeditimg">
        <el-upload
          action=""
          ref="upload"
          v-model="details.img"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-change="imgChange"
          :auto-upload="false"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" size="tiny">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogeditimg = false">取 消</el-button>
          <el-button type="primary" @click="editimg()">确 定</el-button>
        </span>
      </el-dialog>
      <el-col :span="24">
        <el-form-item label="备注" prop="desc">
          <el-input type="textarea" v-model="details.desc"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-form-item>
          <el-button type="primary" @click="update('details')">更新</el-button>
          <el-button @click="goback()">返回</el-button>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formLabelWidth: "120px",
      details: {},
      show: "none",
      TmpDate: "", //保险
      TmpDate1: "", //购车
      TmpDate2: "", //审车
      dialogeditimg: false,
      dialogVisible: false,
      dialogImageUrl: "",
      edit: null,
      show_jg: false,
      key_: "",
      url: "",
      fileList: [],
    };
  },
  created() {
    // console.log(row);
    // 获取文件IP地址
    this.$axios.get("/api/ip").then((res) => {
      if (res.data.status == -1) {
        this.$message({
          message: "配置文件丢失!",
          type: "error",
        });
      } else {
        this.url = res.data.data;
      }
    });
    let id = this.$route.query.uid;
    this.$axios({
      method: "POST",
      url: "/api/findone",
      data: {
        id,
      },
    }).then((res) => {
      this.details = res.data.data[0];
      this.TmpDate1 = this.details.date1; //购车
      this.TmpDate = this.details.date; //买保险
      this.TmpDate2 = this.details.date_sc; //审车
      // 修复错误
      this.details.date = this.details.date == "null" ? "" : this.details.date;
      this.details.date1 =
        this.details.date1 == "null" ? "" : this.details.date1;
      this.details.date_sc =
        this.details.date_sc == "null" ? "" : this.details.date_sc;

      // this.details.date = this.details.date == "null"?
      this.details.buy == "true" || this.details.buy == true
        ? ((this.details.buy = true), (this.show = ""))
        : ((this.details.buy = false), (this.show = "none"));
      //   console.log(this.details);
      if (res.data.data[0].img) {
        this.details.img =
          this.url + res.data.data[0].img.replace("\\", "/").substring(7);
      }
      if (res.data.data[0].img_qs) {
        this.details.img_qs =
          this.url + res.data.data[0].img_qs.replace("\\", "/").substring(7);
      }
      if (res.data.data[0].img_new) {
        this.details.img_new =
          this.url + res.data.data[0].img_new.replace("\\", "/").substring(7);
      }
      if (res.data.data[0].img_jsz) {
        this.details.img_jsz =
          this.url + res.data.data[0].img_jsz.replace("\\", "/").substring(7);
      }
      if (res.data.data[0].img_xszfm) {
        this.details.img_xszfm =
          this.url + res.data.data[0].img_xszfm.replace("\\", "/").substring(7);
      }
    });
  },
  methods: {
    bigimg(n) {
      switch (n) {
        case 0:
          window.open(this.details.img);
          break;
        case 1:
          window.open(this.details.img_xszfm);
          break;
        case 2:
          window.open(this.details.img_qs);
          break;
        case 3:
          window.open(this.details.img_new);
          break;
        case 4:
          window.open(this.details.img_jsz);
          break;
      }
    },
    // 判断男女及生日
    sex() {
      if (this.details.sfz.length == 18) {
        let temp = this.details.sfz;
        this.details.sex = temp.charAt(16) % 2 == 0 ? "女" : "男";
        // 处理生日
        this.details.birthday =
          temp.substring(6, 10) +
          "-" +
          temp.substring(10, 12) +
          "-" +
          temp.substring(12, 14);
        // console.log(this.details.birthday)
      }
    },
    // 是否购车
    changeStatus(callback) {
      this.details.buy = callback;
      callback == true ? (this.show = "") : (this.show = "none");
    },
    // 更新
    update() {

      if (
        this.TmpDate != this.details.date &&
        this.details.date != "" &&
        this.details.date != null
      ) {
        if (this.details.date.length <= 10) {
          console.log();
        } else {
          this.details.date = this.details.date.toLocaleDateString();
        }
        this.details.code = this.details.date.slice(5, 7).replace("/", "");
      } else {
        this.details.date = this.TmpDate;
        this.details.code = this.TmpDate.slice(5, 7).replace("/", "");
      }

      if (
        this.TmpDate2 != this.details.date_sc &&
        this.details.date_sc != "" &&
        this.details.date_sc != null
      ) {
        if (this.details.date_sc.length <= 10) {
          console.log();
        } else {
          this.details.date_sc = this.details.date_sc.toLocaleDateString();
        }
        this.details.code_sc = this.details.date_sc
          .slice(5, 7)
          .replace("/", "");
      } else {
        this.details.date_sc = this.TmpDate2;
        this.details.code_sc = this.TmpDate2.slice(5, 7).replace("/", "");
      }

      // }
      if (
        this.TmpDate1 != this.details.date1 &&
        this.details.date1 != "" &&
        this.details.date1 != null
      ) {
        if (this.details.date1.length <= 10) {
          console.log();
        } else {
          this.details.date1 = this.details.date1.toLocaleDateString();
        }
      } else {
        this.details.date1 = this.TmpDate1;
      }
      this.$axios({
        method: "POST",
        url: "/api/update",
        data: this.details,
      }).then((res) => {
        // console.log(res);
        if (res.data.status === 1) {
          this.$message({
            message: "修改成功!",
            type: "success",
          });
          let id = "LXuao";
          this.$axios({
            method: "POST",
            url: "/api/finduser",
            data: {
              id: id,
            },
          }).then((res) => {
            let id = this.$route.query.uid;
            this.$axios({
              method: "POST",
              url: "/api/findone",
              data: {
                id,
              },
            }).then((res) => {
              this.details = res.data.data[0];
              this.TmpDate1 = this.details.date1; //购车
              this.TmpDate = this.details.date; //买保险
              this.TmpDate2 = this.details.date_sc; //审车
              this.details.buy == "true" || this.details.buy == true
                ? ((this.details.buy = true), (this.show = ""))
                : ((this.details.buy = false), (this.show = "none"));
              if (res.data.data[0].img) {
                this.details.img =
                  this.url +
                  res.data.data[0].img.replace("\\", "/").substring(7);
              }
              if (res.data.data[0].img_xszfm) {
                this.details.img_xszfm =
                  this.url +
                  res.data.data[0].img_xszfm.replace("\\", "/").substring(7);
              }
              if (res.data.data[0].img_qs) {
                this.details.img_qs =
                  this.url +
                  res.data.data[0].img_qs.replace("\\", "/").substring(7);
              }
              if (res.data.data[0].img_new) {
                this.details.img_new =
                  this.url +
                  res.data.data[0].img_new.replace("\\", "/").substring(7);
              }
              if (res.data.data[0].img_jsz) {
                this.details.img_jsz =
                  this.url +
                  res.data.data[0].img_jsz.replace("\\", "/").substring(7);
              }
            });
          });
        }
      });
    },
    handleRemove(file, fileList) {},
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    imgChange(file, fileList) {
      this.edit = file.raw;
    },
    goback() {
      history.back();
    },
    // 点击新增修改
    ed(n) {
      switch (n) {
        case 0:
          this.key_ = 0;
          this.dialogeditimg = true;
          break;
        case 1:
          this.key_ = 1;
          this.dialogeditimg = true;
          break;
        case 2:
          this.key_ = 2;
          this.dialogeditimg = true;
          break;
        case 3:
          this.key_ = 3;
          this.dialogeditimg = true;
          break;
        case 4:
          this.key_ = 4;
          this.dialogeditimg = true;
          break;
      }
    },

    // 显示价格
    jg() {
      this.show_jg = true;
    },
    // 修改行驶证
    editimg(i, row) {
      let formData = new FormData();
      let oldimg = "";
      let url = "";
      if (this.key_ == 0) {
        url = "/api/editimg";
        formData.append("img", this.edit);
        oldimg = this.details.img;
        formData.append("oldimg", oldimg);
      } else if (this.key_ == 1) {
        url = "/api/editimg_xszfm";
        formData.append("img_xszfm", this.edit);
        oldimg = this.details.img_xszfm;
        formData.append("oldimg", oldimg);
      } else if (this.key_ == 2) {
        url = "/api/editimg_qs";
        formData.append("img_qs", this.edit);
        oldimg = this.details.img_qs;
        formData.append("oldimg", oldimg);
      } else if (this.key_ == 3) {
        url = "/api/editimg_new";
        formData.append("img_new", this.edit);
        oldimg = this.details.img_new;
        formData.append("oldimg", oldimg);
      } else if (this.key_ == 4) {
        url = "/api/editimg_jsz";
        formData.append("img_jsz", this.edit);
        oldimg = this.details.img_jsz;
        formData.append("oldimg", oldimg);
      }
      formData.append("_id", this.details._id);
      // console.log(this.details);
      this.$axios({
        method: "POST",
        url: url,
        data: formData,
      }).then((res) => {
        if (res.data.status === 1) {
          this.dialogeditimg = false;
          this.$refs.upload.clearFiles();
          this.$message({
            message: "修改成功!",
            type: "success",
          });
          let id = this.$route.query.uid;
          this.$axios({
            method: "POST",
            url: "/api/findone",
            data: {
              id,
            },
          }).then((res) => {
            if (res.data.data[0].img) {
              this.details.img =
                this.url + res.data.data[0].img.replace("\\", "/").substring(7);
            }
            if (res.data.data[0].img_xszfm) {
              this.details.img_xszfm =
                this.url +
                res.data.data[0].img_xszfm.replace("\\", "/").substring(7);
            }
            if (res.data.data[0].img_qs) {
              this.details.img_qs =
                this.url +
                res.data.data[0].img_qs.replace("\\", "/").substring(7);
            }
            if (res.data.data[0].img_new) {
              this.details.img_new =
                this.url +
                res.data.data[0].img_new.replace("\\", "/").substring(7);
            }
            if (res.data.data[0].img_jsz) {
              this.details.img_jsz =
                this.url +
                res.data.data[0].img_jsz.replace("\\", "/").substring(7);
            }
          });
        }
      });
    },
  },
  components: {},
};
</script>

<style>
.nav {
  height: 30px;
  padding-top: 15px;
  font-size: 20px;
}
/* .el-input {
  width: 120px;
} */
.el-form-item__content {
  text-align: left;
}
.el-main[data-v-a83bd3b0] {
  text-align: left !important;
  height: 820px !important;
}
.image-slot {
  text-align: center;
  color: red;
  background: #f5f7fa;
}
.el-image__error,
.el-image__inner,
.el-image__placeholder {
  height: 200px;
  width: 200px;
}
</style>
